* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}
.currency-converter {
  width: 400px;
  background-color: #fff;
  border-radius: 8px;
  padding: 20px 20px;
  box-shadow: 2px 2px 5px white;
  margin-top: 50px;
}
h1 {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 25px;
  padding: 5px 5px;
  margin-bottom: 5px;
  border-bottom:5px double rgb(14, 13, 13) ;
  color: rgb(252, 14, 14);
  text-align: center;
}
.box {
  width: 100%;
  height: 200px;
  background-image: url("./assets/currency.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
}
.input-container {
  margin-bottom: 15px;
  margin-top: 10px;
}
label {
  display: block;
  margin-bottom: 5px ;
  color: rgb(12, 12, 12);
  font-weight: 700;
}
input,select {
  width: 100%;
  padding: 6px;
  margin-bottom: 5px;
  border: 3px solid rgb(10, 10, 10);
  border-radius: 5px;
  outline: none;
  font-size: 15px;
}
input:focus,
select:focus {
  border-color: rgb(14, 204, 252);
}
.result {
  margin:10px 0;
  padding: 10px;
  border-radius: 10px;
  background-color: black;
}
.result p {
  color: rgb(245, 50, 50);
  text-align: center;
  font-weight: 900;
  font-size: 20px;
}
